import React ,{ useEffect } from "react";
import { ScrollTrigger } from "gsap/all";
import { gsap } from "gsap";

export default function Skill(){

    //About 왼쪽 스킬 액션 - opacity 변경, 위치고정, Y값변경 타임라인 
    gsap.registerPlugin(ScrollTrigger);
    useEffect(()=>{
        let skillTl = gsap.timeline({
            scrollTrigger:{
                trigger:".skill_good",
                start:"top top",
                end:"bottom center",
                pin: true,
                scrub: true,
                anticipatePin: 1,
            }
        })
        let skillTl2 = gsap.timeline({
            scrollTrigger:{
                trigger:".skill_mid",
                start:"top top",
                end:"bottom center",
                pin: true,
                scrub: true,
                anticipatePin: 1,
            }
        })
        let skillTl3 = gsap.timeline({
            duration: 0.5,
            scrollTrigger:{
                trigger:".skill_little",
                start:"top top",
                end:"bottom center",
                pin: true,
                scrub: true,
                anticipatePin: 1,
            }
        })
        // skillTl.to(".skill_good >h3 >span",{
        //     y:"0%",
        //     autoAlpha:1,
        // });
        skillTl.to(".skill_good >p >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl.to(".skill_good",{
            opacity:0,
        })
        // skillTl2.to(".skill_mid >h3 >span",{
        //     y:"0%",
        //       autoAlpha:1,
        // });
        skillTl2.to(".skill_mid >p >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl2.to(".skill_mid",{
            opacity:0,
            duration: 0.1,
        })
        // skillTl3.to(".skill_little >h3 >span",{
        //     y:"0%",
        //     autoAlpha:1,
        // });
        skillTl3.to(".skill_little >p >span",{
            y:"0%",
            autoAlpha:1,
        });
        skillTl3.to(".skill_little",{
            opacity:0,
        })
    },[]);
 
    return(
        <div className="skill">
            <div className="skill_good">
                <h3><span>비중있게 다루고 있고 잘 사용합니다.</span></h3>
                <p>
                    <span>HTML</span>
                    <span>웹 표준 준수 및 효율적인 구조를 최우선적인 목표로 가지며 크로스 브라우징 이슈에 대해 인지하고 있습니다</span>
                </p>
                <p>
                    <span>CSS / SCSS</span>
                    <span>웹 표준 준수 하며 브라우저 크기에 대응하는 미디어쿼리 사용이 가능합니다</span>
                </p>
                <p>
                    <span>Jquery / JavaScript</span>
                    <span>plug-in 사용 및 스크립트 구현이 가능합니다</span>
                </p>
                <p>
                    <span>REACT</span>
                    <span>plug-in 사용 및 스크립트 구현이 가능합니다</span>
                </p>
            </div>
            <div className="skill_mid">
                <h3><span>사용경험이 있으며 간단한 작업이 가능합니다</span></h3>
                <p>
                    <span>PHP</span>
                    <span>로그인, 게시판관리 구현 경험있습니다</span>
                </p>
                <p>
                    <span>webpack</span>
                    <span>모듈화 가능합니다</span>
                </p>
                <p>
                    <span>PS</span>
                    <span>합성, 사진 편집 등의 2D 그래픽 작업을 비롯한 다양한 웹 시안 작업이 가능합니다</span>
                </p>
                <p>
                    <span>AI</span>
                    <span>간단한 툴 사용 및 편집이 가능합니다</span>
                </p>
            </div>
            <div className="skill_little">
                <h3><span>배움을 즐기는 사람입니다</span></h3>
                <p>
                    <span>모르면 반복하고 또 반복해서 깨달음을 얻는 순간을 즐깁니다.</span>
                    <span></span>
                </p>
            </div>
        </div>
    )
}